<template>
  <div>
    <v-container>
      <v-row>
        <v-col cols="12">
          <!-- data table -->
          <crud-data-table
            :table="{
              heading: $t('crud.table.headings.utilities.icons'),
              showSelect: true,
              search: true,
              button: {
                text: $t('crud.table.addNewButtons.utilities.icons')
              }
            }"
            :add-edit-dialog="{
              addHeading: $t('addEditDialog.headings.add.utilities.icons'),
              editHeading: $t('addEditDialog.headings.edit.utilities.icons')
            }"
            :headers="headers"
            table-heading="Icons"
            :default-item="item"
            :actions="{
              edit: true,
              delete: true
            }"
            :fetch-data="{
              url: '/api/icons',
              data: 'icons'
            }"
            :add-data="{
              url: '/api/icons',
              reload: true
            }"
            :edit-data="{
              url: '/api/icons/:param',
              param: '_id',
              reload: true
            }"
            :delete-data="{
              url: '/api/icons/:param',
              param: '_id',
              reload: true
            }"
            :delete-multiple-data="{
              url: '/api/icons/delete',
              arrayOf: '_id',
              reload: true
            }"
            @after-add="$store.dispatch('icons/fetchIcons', { axios: $axios })"
            @after-edit="$store.dispatch('icons/fetchIcons', { axios: $axios })"
            @after-delete="
              $store.dispatch('icons/fetchIcons', { axios: $axios })
            "
          >
            <template v-slot:[`item.icon`]="{ item: icon }">
              <v-icon>{{ icon.icon }}</v-icon>
            </template>
            <template v-slot:crud-form="{ item: icon }">
              <icon-form :icon="icon" />
            </template>
          </crud-data-table>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
export default {
  components: {
    CrudDataTable: () => import('@/components/crud/CrudDataTable'),
    IconForm: () => import('@/components/forms/utilities/IconForm')
  },
  middleware: ['pagePermission'],
  data() {
    return {
      headers: [
        { text: this.$t('crud.table.headers.title'), value: 'title' },
        { text: this.$t('crud.table.headers.slug'), value: 'slug' },
        { text: this.$t('crud.table.headers.icon'), value: 'icon' },
        {
          text: this.$t('crud.table.headers.actions'),
          value: 'actions',
          sortable: false
        }
      ],
      item: { title: '' }
    }
  }
}
</script>

